import React, { Component } from 'react'
import moment from 'moment'

export default class Code extends Component {
  constructor(props){
    super(props);
    this.state = {
      time: moment().format('YYYY-MM-DD HH:mm:ss'),
      code: "-",
      dcode: "-"
    }
  }

  componentDidMount() {
    this.timer = setInterval(
      () => {
        const number = (parseInt(moment().format('MMDDHH')) * 91).toString().split('').reverse()
        const dnumber = (Math.pow(parseInt(moment().format('YYDDMM')),2) * 7).toString().split('').reverse()
        this.setState({
          time: moment().format('YYYY-MM-DD HH:mm:ss'),
          code: number.slice(0,4),
          dcode: dnumber.slice(5,9)
        })
      },
      500
    );
  }
  componentWillUnmount() {
    this.timer && clearInterval(this.timer);
  }
  render() {
    const {code, time, dcode} = this.state
    return (
      <div>
        {time} 
        <br/>
        {code}
        <br/>
        D{dcode}
      </div>
    )
  }
}
